// 获取common.js中的服务器地址
//import common from "../../common/common.js";

//导入自定义组件
import trailerStars from "../../components/trailerStars.vue"

export default {
	data() {
		return {
			carouselList: [],
			hotSuperHeroList: [],
			hotTrailerList: [],
			guessULikeList: [],
			animationData: {},
			animationDataArr: [{}, {}, {}, {}, {}, {}]
		}
	},
	onUnload() {
		// 页面卸载的时候，清除动画对象
		this.animationData = {};
		this.animationDataArr = [{}, {}, {}, {}, {}, {}];
	},
	onPullDownRefresh() {
		this.refresh();
	},
	onLoad() {
		var me = this;


		// 在页面创建的时候，创建一个临时动画对象
		this.animation = uni.createAnimation();


		// 获取common.js中的服务器地址
		//var serverUrl = common.serverUrl;
		// 通过挂载到main.js中获取服务器的地址，作为全局变量
		var serverUrl = me.serverUrl;

		// 请求轮播图数据
		uni.request({
			url: serverUrl + '/index/Carousel',
			method: "POST",
			success: (res) => {
				// 获取真实数据之前，务必判断状态是否为200
				if (res.statusCode == 200) {
					var carouselList = res.data;
					me.carouselList = carouselList;
				}
			}
		});

		// 查询热门超英
		uni.request({
			url: serverUrl + '/index/movie/hot?type=superhero',
			method: "POST",
			success: (res) => {
				// 获取真实数据之前，务必判断状态是否为200
				if (res.statusCode == 200) {
					var hotSuperHeroList = res.data;
					me.hotSuperHeroList = hotSuperHeroList;
				}
			}
		});

		// 查询热门超英
		uni.request({
			url: serverUrl + '/index/movie/hot?type=trailer',
			method: "POST",
			success: (res) => {
				// 获取真实数据之前，务必判断状态是否为200
				if (res.statusCode == 200) {
					var hotTrailerList = res.data;
					me.hotTrailerList = hotTrailerList;
				}
			}
		});
		
		me.refresh();
	},
	methods: {
		//刷新页面数据
		refresh() {
			var me = this;
			uni.showLoading({
				mask:true
			});
			
			uni.showNavigationBarLoading();
			
			//中获取服务器的地址
			// 通过挂载到main.js，作为全局变量
			var serverUrl = me.serverUrl;

			// 查询猜你喜欢数据列表
			uni.request({
				url: serverUrl + '/index/guessULike',
				method: "POST",
				success: (res) => {
					// 获取真实数据之前，务必判断状态是否为200
					if (res.statusCode == 200) {
						var guessULikeList = res.data;
						me.guessULikeList = guessULikeList;
					}
				},complete() {
					uni.stopPullDownRefresh();
					uni.hideLoading();
					uni.hideNavigationBarLoading();
				}
			});
		},
		// 实现动画点赞效果
		praiseMe(e) {

			var gIndex = e.currentTarget.dataset.gindex;
			// return;
			// 构建动画数据，并且通过step来表示这组动画的完成
			this.animation.translateY(-60).opacity(1).step({
				duration: 500
			});

			// 导出动画数据到view组件，实现组件的动画效果
			// this.animationData = this.animation.export();
			this.animationData = this.animation;
			this.animationDataArr[gIndex] = this.animationData.export();

			// 还原动画
			setTimeout(function() {
				this.animation.translateY(0).opacity(0).step({
					duration: 0
				});
				this.animationData = this.animation;
				this.animationDataArr[gIndex] = this.animationData.export();
			}.bind(this), 600)

		}
	},

	components: {
		trailerStars
	}
}
